












































































































	export default{
		data(){
			return{
				tabLists: [
          {
            name: '全部',
            status: ''
          },
          {
            name: '招代理',
            status: 1
          },
          {
            name: '拓业务',
            status: 2
          },
          {
            name: '正能量',
            status: 3
          },
          {
            name: '活动',
            status: 4
          }
        ],
        activeId: 0, // 当前选中tab
        lists: [],
        sortLists: [
          {
            name: '朋友圈文案',
            status: 20,
            choice: false
          },
          {
            name: '图文长文',
            status: 40,
            choice: false
          },
          {
            name: '视频资料',
            status: 30,
            choice: false
          },
          {
            name: '常用话术',
            status: 10,
            choice: false
          }
        ],
        sortStatusArr: [],
        showSort: false,
        zaning: false, // 点赞操作中
        currentPage: 1, // 当前页
        maxPage: 1, // 最大页
        loading: false, // 加载中
        searchStr: '', // 搜索字段
        searching: false, // 搜索状态
        inputing: false,
        pictureShow: false,
        pictureUrlArr: '',
        videoShow: false,
        videoUrl: '',
        popPlaying: false,
			}
		},
    computed: {
      userInfo() {
        return this.$store.state.userInfo
      },
      role() {
        return this.userInfo.role
      },
      isLoad() {
        return parseInt(this.maxPage) <= parseInt(this.currentPage) && this.lists.length > 0
      },
      defaultAvatar() {
        return '../../static/images/avatar.png'
      },
      showSearch() {
        return (this.lists.length > 0 && !this.loading) || this.searching
      },
      showNoRecord() {
        return this.lists.length <= 0 && !this.loading
      }
    },
    onLoad() {
    	this.updateLists(this.activeId)
    },
		methods:{
			tabChange(n) {
        if (this.activeId === n) return
        if (this.loading) {
          uni.showToast({
          	title: '正在加载数据',
            icon: 'none'
          })
          return
        }
        this.activeId = n
        this.sortStatus = 0
        this.sortType = 1
        this.searchStr = ''
        this.searching = false
        this.lists.length = 0
        this.currentPage = 1
        this.sortLists.forEach(item => {
          item.choice = false
        })
        this.sortStatusArr = []
        this.updateLists(n)
      },
      updateLists(n) {
        if (this.loading) return
        this.loading = true
        if (parseInt(this.currentPage) <= 1) {
          this.lists = []
        }
        let reqStr = `?label=${this.activeId}&search=${this.searchStr}&page=${this.currentPage}`
        let typeStr = ''
        if (this.sortStatusArr.length > 0) {
          this.sortStatusArr.forEach(item => {
            typeStr += `&type[]=${item}`
          })
          reqStr += typeStr
        }
        setTimeout(() => {
          this.$http({
            url: this.$apis.material + reqStr,
            method: 'GET',
            cb: (err, data) => {
              if (!err && data.success) {
                this.currentPage = data.data.current_page
                this.maxPage = data.data.last_page
                let oldArr = this.lists
                let newArr = data.data.data
                this.lists = oldArr.concat(newArr)
                this.lists.forEach(item => {
                  this.$set(item, 'opened', 0)
                })
                if (this.lists.length > 0) {
                  setTimeout(() => {
                    this.countIntroHeight()
                  }, 0)
                }
                if (this.searchStr.length > 0) this.searching = true
              } else {
                this.searching = false
                uni.showToast({
                	title: '数据获取失败',
                  icon: 'none'
                })
              }
              this.loading = false
            }
          })
        }, 50)
      },
      showSortPop() {
        this.showSort = true
      },
      hideSortPop() {
        this.showSort = false
      },
      clickSort(n) {
        this.hideSortPop()
        this.sortLists[n].choice = !this.sortLists[n].choice
        this.sortStatusArr = []
        this.sortLists.forEach(item => {
          if (item.choice) {
            this.sortStatusArr.push(item.status)
          }
        })
        this.searchStr = ''
        this.searching = false
        this.currentPage = 1
        this.updateLists(this.activeId)
      },
      clearSort() {
        this.sortLists.forEach(item => {
          item.choice = false
        })
        this.sortStatusArr = []
        this.updateLists(this.activeId)
      },
      search() {
        if (this.searchStr.length <= 0) {
          uni.showToast({
          	title: '请输入搜索内容',
            icon: 'none'
          })
          return
        }
        this.currentPage = 1
        this.updateLists(this.activeId)
      },
      resetSearch() {
        this.currentPage = 1
        this.searchStr = ''
        this.searching = false
        this.updateLists(this.activeId)
      },
      scrollUpdate() {
        if (!this.loading && !this.isLoad && this.lists.length > 0) {
          this.currentPage ++
          this.updateLists(this.activeId)
        }
      },
      copy(str, type) {
        uni.setClipboardData({
        	data: str,
          success: () => {
            if (type === '视频') {
              uni.showModal({
                title: '复制成功',
                content: '文案已复制，请下载视频去分享',
                showCancel: false
              })
            } else if (type === '图文') {
              uni.showModal({
                title: '复制成功',
                content: '文案已复制，请下载图片去分享',
                showCancel: false
              })
            } else if (type === '链接') {
              uni.showModal({
                title: '复制成功',
                content: '文案已复制，请打开链接去分享',
                showCancel: false
              })
            } else {
              uni.showModal({
                title: '复制成功',
                content: '文案已复制，请打开链接去分享',
                showCancel: false
              })
            }
          },
          fail: () => {
            uni.showModal({
              title: '复制失败',
              content: str,
              showCancel: false
            })
          }
        })
      },
      countIntroHeight() {
        
      },
      introShow(item) {
        if (item.opened === 1) {
          item.opened = 2
        } else {
          item.opened = 1
        }
      },
      clickZan(item) {
        this.$http({
          url: this.$apis.materialStar,
          method: 'POST',
          data: {
            id: item.id
          },
          cb: (err, data) => {
            if (!err && data.success) {
              item.star = data.data
              item.has_star = !item.has_star
            } else {
              uni.showToast({
              	title: '点赞失败，请稍后再试',
                icon: 'none'
              })
            }
          }
        })
      },
      showPopPicture(arr, index) {
        uni.previewImage({
          current: arr[index],
          indicator: 'default',
          urls: arr
        })
      },
      closePopPic() {
        
      },
      showPopVideo(url) {
        this.videoShow = true
        this.videoUrl = url
        uni.createVideoContext('popVideo').play()
      },
      closePopVideo() {
        this.videoShow = false
        uni.createVideoContext('popVideo').pause()
      },
      downloadVideo() {
        
      },
      gotoContent(id) {
        this.$navigateTo({
        	url: '/pages/spread/essay?eId=' + id
        })
      }
		}
	}
	
